<template>
    <div class="main-page">
        <template v-if="showHeader">
            <q-card   class="page-section q-py-sm q-px-md q-mb-md nice-shadow-18">
                <div class="container">
                    <div class="row justify-between q-col-gutter-md">
                        <div class="col-12 col-md-auto ">
                            <div class="">
                                <div class="row  items-center q-col-gutter-sm q-px-sm">
                                    <div class="col">
                                        <div class="text-h6 text-primary">Add New Operator Certificate Applications</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </q-card>
        </template>
        <section class="page-section q-mb-md">
            <div class="container">
                <div class="row q-col-gutter-md">
                    <div class="col-md-9 col-12 comp-grid">
                        <q-card   class="q-pa-md nice-shadow-18">
                            <div>
                                <ValidationObserver ref="observer" v-slot="{ invalid }" tag="form" @submit.prevent="startSaveRecord()" @reset="resetForm">
                                    <div class="row q-col-gutter-md">
                                        <div class="col-12">
                                            <div class="row">
                                                <div class="col-sm-3 col-12">
                                                    Certicate Application Type Id *
                                                </div>
                                                <div class="col-sm-9 col-12">
                                                    <api-data-source @loaded="response => mapOptionField(response, 'certicate_application_type_id')"  api-path="components_data/certicate_application_type_id_option_list_2"  v-slot="req">
                                                        <ValidationProvider :rules="{required:true}" name="Certicate Application Type Id" v-slot="{ errors, invalid, validated }">
                                                            <q-select   :loading="req.loading"  outlined dense  ref="ctrlcerticate_application_type_id" emit-value map-options  v-model="formData.certicate_application_type_id" :options="req.response" label="Certicate Application Type Id"  :error="invalid && validated" :error-message="errors[0]" >
                                                            <template v-slot:option="scope">
                                                                <q-item v-bind="scope.itemProps" v-on="scope.itemEvents">
                                                                    <q-item-section v-if="scope.opt.image" avatar>
                                                                        <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                    </q-item-section>
                                                                    <q-item-section>
                                                                        <q-item-label />{{ scope.opt.label }}
                                                                            <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                        </q-item-section>
                                                                    </q-item>
                                                                </template>
                                                                <template v-slot:selected-item="scope">
                                                                    <q-item  v-if="scope.opt">
                                                                        <q-item-section v-if="scope.opt.image" avatar>
                                                                            <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                        </q-item-section>
                                                                        <q-item-section>
                                                                            <q-item-label />{{ scope.opt.label }}
                                                                                <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                            </q-item-section>
                                                                        </q-item>
                                                                    </template>
                                                                    </q-select> 
                                                                </ValidationProvider>
                                                            </api-data-source>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="col-12">
                                                    <div class="row">
                                                        <div class="col-sm-3 col-12">
                                                            Submitted Application Id *
                                                        </div>
                                                        <div class="col-sm-9 col-12">
                                                            <api-data-source @loaded="response => mapOptionField(response, 'submitted_application_id')"  api-path="components_data/submitted_application_id_option_list"  v-slot="req">
                                                                <ValidationProvider :rules="{required:true}" name="Submitted Application Id" v-slot="{ errors, invalid, validated }">
                                                                    <q-select   :loading="req.loading"  outlined dense  ref="ctrlsubmitted_application_id" emit-value map-options  v-model="formData.submitted_application_id" :options="req.response" label="Submitted Application Id"  :error="invalid && validated" :error-message="errors[0]" >
                                                                    <template v-slot:option="scope">
                                                                        <q-item v-bind="scope.itemProps" v-on="scope.itemEvents">
                                                                            <q-item-section v-if="scope.opt.image" avatar>
                                                                                <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                            </q-item-section>
                                                                            <q-item-section>
                                                                                <q-item-label />{{ scope.opt.label }}
                                                                                    <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                                </q-item-section>
                                                                            </q-item>
                                                                        </template>
                                                                        <template v-slot:selected-item="scope">
                                                                            <q-item  v-if="scope.opt">
                                                                                <q-item-section v-if="scope.opt.image" avatar>
                                                                                    <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                                </q-item-section>
                                                                                <q-item-section>
                                                                                    <q-item-label />{{ scope.opt.label }}
                                                                                        <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                                    </q-item-section>
                                                                                </q-item>
                                                                            </template>
                                                                            </q-select> 
                                                                        </ValidationProvider>
                                                                    </api-data-source>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div class="col-12">
                                                            <div class="row">
                                                                <div class="col-sm-3 col-12">
                                                                    Operator Certificate Type Id *
                                                                </div>
                                                                <div class="col-sm-9 col-12">
                                                                    <api-data-source @loaded="response => mapOptionField(response, 'operator_certificate_type_id')"  api-path="components_data/operator_certificate_type_id_option_list"  v-slot="req">
                                                                        <ValidationProvider :rules="{required:true}" name="Operator Certificate Type Id" v-slot="{ errors, invalid, validated }">
                                                                            <q-select   :loading="req.loading"  outlined dense  ref="ctrloperator_certificate_type_id" emit-value map-options  v-model="formData.operator_certificate_type_id" :options="req.response" label="Operator Certificate Type Id"  :error="invalid && validated" :error-message="errors[0]" >
                                                                            <template v-slot:option="scope">
                                                                                <q-item v-bind="scope.itemProps" v-on="scope.itemEvents">
                                                                                    <q-item-section v-if="scope.opt.image" avatar>
                                                                                        <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                                    </q-item-section>
                                                                                    <q-item-section>
                                                                                        <q-item-label />{{ scope.opt.label }}
                                                                                            <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                                        </q-item-section>
                                                                                    </q-item>
                                                                                </template>
                                                                                <template v-slot:selected-item="scope">
                                                                                    <q-item  v-if="scope.opt">
                                                                                        <q-item-section v-if="scope.opt.image" avatar>
                                                                                            <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                                        </q-item-section>
                                                                                        <q-item-section>
                                                                                            <q-item-label />{{ scope.opt.label }}
                                                                                                <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                                            </q-item-section>
                                                                                        </q-item>
                                                                                    </template>
                                                                                    </q-select> 
                                                                                </ValidationProvider>
                                                                            </api-data-source>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                                <div class="col-12">
                                                                    <div class="row">
                                                                        <div class="col-sm-3 col-12">
                                                                            Registration No *
                                                                        </div>
                                                                        <div class="col-sm-9 col-12">
                                                                            <ValidationProvider :rules="{required:true}" name="Registration No" v-slot="{ errors, invalid, validated }">
                                                                                <q-input outlined dense  ref="ctrlregistration_no" v-model="formData.registration_no"  label="Registration No" type="text" placeholder="Enter Registration No"   list="registration_no_list"  
                                                                                class="" :error="invalid && validated" :error-message="errors[0]">
                                                                                </q-input>
                                                                                <datalist id="registration_no_list">
                                                                                <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                                                </datalist>
                                                                            </ValidationProvider>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                                <div class="col-12">
                                                                    <div class="row">
                                                                        <div class="col-sm-3 col-12">
                                                                            Place Of Exam *
                                                                        </div>
                                                                        <div class="col-sm-9 col-12">
                                                                            <ValidationProvider :rules="{required:true}" name="Place Of Exam" v-slot="{ errors, invalid, validated }">
                                                                                <q-input outlined dense  ref="ctrlplace_of_exam" v-model="formData.place_of_exam"  label="Place Of Exam" type="text" placeholder="Enter Place Of Exam"   list="place_of_exam_list"  
                                                                                class="" :error="invalid && validated" :error-message="errors[0]">
                                                                                </q-input>
                                                                                <datalist id="place_of_exam_list">
                                                                                <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                                                </datalist>
                                                                            </ValidationProvider>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                                <div class="col-12">
                                                                    <div class="row">
                                                                        <div class="col-sm-3 col-12">
                                                                            Date Of Exam *
                                                                        </div>
                                                                        <div class="col-sm-9 col-12">
                                                                            <ValidationProvider :rules="{required:true}" name="Date Of Exam" v-slot="{ errors, invalid, validated }">
                                                                                <q-input outlined dense  v-model="formData.date_of_exam" :error="invalid && validated" :error-message="errors[0]" >
                                                                                <template v-slot:prepend>
                                                                                    <q-icon name="date_range" class="cursor-pointer">
                                                                                    <q-popup-proxy transition-show="scale" transition-hide="scale">
                                                                                    <q-date     mask="YYYY-MM-DD HH:mm" v-model="formData.date_of_exam" />
                                                                                    </q-popup-proxy>
                                                                                    </q-icon>
                                                                                </template>
                                                                                <template v-slot:append>
                                                                                    <q-icon name="access_time" class="cursor-pointer">
                                                                                    <q-popup-proxy transition-show="scale" transition-hide="scale">
                                                                                    <q-time v-model="formData.date_of_exam" mask="YYYY-MM-DD HH:mm" />
                                                                                    </q-popup-proxy>
                                                                                    </q-icon>
                                                                                </template>
                                                                                </q-input>
                                                                            </ValidationProvider>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                                <div class="col-12">
                                                                    <div class="row">
                                                                        <div class="col-sm-3 col-12">
                                                                            Rating *
                                                                        </div>
                                                                        <div class="col-sm-9 col-12">
                                                                            <ValidationProvider :rules="{required:true}" name="Rating" v-slot="{ errors, invalid, validated }">
                                                                                <q-input outlined dense  ref="ctrlrating" v-model="formData.rating"  label="Rating" type="number" placeholder="Enter Rating"   step="any" list="rating_list"  
                                                                                class="" :error="invalid && validated" :error-message="errors[0]">
                                                                                </q-input>
                                                                                <datalist id="rating_list">
                                                                                <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                                                </datalist>
                                                                            </ValidationProvider>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div v-if="showSubmitButton" class="text-center q-my-md">
                                                                <q-btn type="submit"    :rounded="false"  color="primary"  no-caps  unelevated   :disabled="invalid" icon="send" :loading="saving">
                                                                    <q-spinner-oval slot="loading" /> Submit
                                                                </q-btn>
                                                            </div>
                                                        </ValidationObserver>
                                                        <slot :submit="submit" :saving="saving"></slot>
                                                    </div>
                                                </q-card>
                                            </div>
                                        </div>
                                    </div>
                                </section>
                            </div>
                        </template>
                        <script>
	import { PageMixin } from "../../mixins/page.js";
	import { AddPageMixin } from "../../mixins/addpage.js";
	import { mapActions } from "vuex";
	export default {
		name: 'addOperatorcertificateapplicationsPage',
		components: {
		},
		mixins: [PageMixin, AddPageMixin ],
		props:{
			pageName : {
				type : String,
				default : 'operator_certificate_applications',
			},
			routeName : {
				type : String,
				default : 'operator_certificate_applicationsadd',
			},
			apiPath : {
				type : String,
				default : 'operator_certificate_applications/add',
			},
		},
		data() {
            return {
				formData: {
					certicate_application_type_id: "", submitted_application_id: "", operator_certificate_type_id: "", registration_no: "", place_of_exam: "", date_of_exam: "", rating: "", 
				},
				date_of_examPicker:false
			}
		},
		computed: {
			pageTitle:{
				get: function () {
					return "Add New Operator Certificate Applications"
				}
			}
		},
		meta () {
			return {
				title: this.pageTitle
			}
		},
		methods: {
			...mapActions('operator_certificate_applications', ['saveRecord']),
			async startSaveRecord(){
				const isFormValid = await this.$refs.observer.validate();
				if(isFormValid){
					this.saving = true;
					let payload = this.normalizedFormData();
					let url = this.apiUrl;
					let data = { url, payload  }
					this.saveRecord(data).then((response) => {
						this.record = response.data;
						this.id = this.record['id'];
						this.saving = false;
						this.resetForm();
						this.closeDialogs();// close page dialog that if opened
						this.flashMsg(this.msgAfterSave);
						this.navigateTo(`/operator_certificate_applications`);
					},
					 (response) => {
						this.saving = false;
						this.showPageRequestError(response);
					});
				}
			},
			resetForm (){
				this.formData = {certicate_application_type_id: "", submitted_application_id: "", operator_certificate_type_id: "", registration_no: "", place_of_exam: "", date_of_exam: "", rating: "", };
				requestAnimationFrame(() => {
					this.$refs.observer.reset();
				});
				this.$EventBus.$emit("resetForm");
			},
		},
		mounted() {
		},
	};
</script>
<style scoped>
</style>
