<template>
    <div class="main-page">
        <template v-if="showHeader">
            <q-card   class="page-section q-py-sm q-px-md q-mb-md nice-shadow-18">
                <div class="container">
                    <div class="row justify-between q-col-gutter-md">
                        <div class="col-12 col-md-auto ">
                            <div class="">
                                <div class="row  items-center q-col-gutter-sm q-px-sm">
                                    <div class="col">
                                        <div class="text-h6 text-primary">Add New Forms</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </q-card>
        </template>
        <section class="page-section q-mb-md">
            <div class="container">
                <div class="row q-col-gutter-md">
                    <div class="col-md-9 col-12 comp-grid">
                        <q-card   class="q-pa-md nice-shadow-18">
                            <div>
                                <ValidationObserver ref="observer" v-slot="{ invalid }" tag="form" @submit.prevent="startSaveRecord()" @reset="resetForm">
                                    <div class="row q-col-gutter-md">
                                        <div class="col-12">
                                            <div class="row">
                                                <div class="col-sm-3 col-12">
                                                    Application Id *
                                                </div>
                                                <div class="col-sm-9 col-12">
                                                    <api-data-source @loaded="response => mapOptionField(response, 'application_id')"  api-path="components_data/application_id_option_list_2"  v-slot="req">
                                                        <ValidationProvider :rules="{required:true}" name="Application Id" v-slot="{ errors, invalid, validated }">
                                                            <q-select   :loading="req.loading"  outlined dense  ref="ctrlapplication_id" emit-value map-options  v-model="formData.application_id" :options="req.response" label="Application Id"  :error="invalid && validated" :error-message="errors[0]" >
                                                            <template v-slot:option="scope">
                                                                <q-item v-bind="scope.itemProps" v-on="scope.itemEvents">
                                                                    <q-item-section v-if="scope.opt.image" avatar>
                                                                        <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                    </q-item-section>
                                                                    <q-item-section>
                                                                        <q-item-label />{{ scope.opt.label }}
                                                                            <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                        </q-item-section>
                                                                    </q-item>
                                                                </template>
                                                                <template v-slot:selected-item="scope">
                                                                    <q-item  v-if="scope.opt">
                                                                        <q-item-section v-if="scope.opt.image" avatar>
                                                                            <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                        </q-item-section>
                                                                        <q-item-section>
                                                                            <q-item-label />{{ scope.opt.label }}
                                                                                <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                            </q-item-section>
                                                                        </q-item>
                                                                    </template>
                                                                    </q-select> 
                                                                </ValidationProvider>
                                                            </api-data-source>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="col-12">
                                                    <div class="row">
                                                        <div class="col-sm-3 col-12">
                                                            Field Group Id *
                                                        </div>
                                                        <div class="col-sm-9 col-12">
                                                            <api-data-source @loaded="response => mapOptionField(response, 'field_group_id')"  api-path="components_data/field_group_id_option_list"  v-slot="req">
                                                                <ValidationProvider :rules="{required:true}" name="Field Group Id" v-slot="{ errors, invalid, validated }">
                                                                    <q-select   :loading="req.loading"  outlined dense  ref="ctrlfield_group_id" emit-value map-options  v-model="formData.field_group_id" :options="req.response" label="Field Group Id"  :error="invalid && validated" :error-message="errors[0]" >
                                                                    </q-select> 
                                                                </ValidationProvider>
                                                            </api-data-source>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="col-12">
                                                    <div class="row">
                                                        <div class="col-sm-3 col-12">
                                                            Revision No *
                                                        </div>
                                                        <div class="col-sm-9 col-12">
                                                            <ValidationProvider :rules="{required:true}" name="Revision No" v-slot="{ errors, invalid, validated }">
                                                                <q-input outlined dense  ref="ctrlrevision_no" v-model="formData.revision_no"  label="Revision No" type="text" placeholder="Enter Revision No"   list="revision_no_list"  
                                                                class="" :error="invalid && validated" :error-message="errors[0]">
                                                                </q-input>
                                                                <datalist id="revision_no_list">
                                                                <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                                </datalist>
                                                            </ValidationProvider>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="col-12">
                                                    <div class="row">
                                                        <div class="col-sm-3 col-12">
                                                            Revion Date *
                                                        </div>
                                                        <div class="col-sm-9 col-12">
                                                            <ValidationProvider :rules="{required:true}" name="Revion Date" v-slot="{ errors, invalid, validated }">
                                                                <q-input outlined dense  v-model="formData.revion_date" :error="invalid && validated" :error-message="errors[0]" >
                                                                <template v-slot:prepend>
                                                                    <q-icon name="date_range" class="cursor-pointer">
                                                                    <q-popup-proxy transition-show="scale" transition-hide="scale">
                                                                    <q-date     mask="YYYY-MM-DD HH:mm" v-model="formData.revion_date" />
                                                                    </q-popup-proxy>
                                                                    </q-icon>
                                                                </template>
                                                                <template v-slot:append>
                                                                    <q-icon name="access_time" class="cursor-pointer">
                                                                    <q-popup-proxy transition-show="scale" transition-hide="scale">
                                                                    <q-time v-model="formData.revion_date" mask="YYYY-MM-DD HH:mm" />
                                                                    </q-popup-proxy>
                                                                    </q-icon>
                                                                </template>
                                                                </q-input>
                                                            </ValidationProvider>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div v-if="showSubmitButton" class="text-center q-my-md">
                                                <q-btn type="submit"    :rounded="false"  color="primary"  no-caps  unelevated   :disabled="invalid" icon="send" :loading="saving">
                                                    <q-spinner-oval slot="loading" /> Submit
                                                </q-btn>
                                            </div>
                                        </ValidationObserver>
                                        <slot :submit="submit" :saving="saving"></slot>
                                    </div>
                                </q-card>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        </template>
        <script>
	import { PageMixin } from "../../mixins/page.js";
	import { AddPageMixin } from "../../mixins/addpage.js";
	import { mapActions } from "vuex";
	export default {
		name: 'addFormsPage',
		components: {
		},
		mixins: [PageMixin, AddPageMixin ],
		props:{
			pageName : {
				type : String,
				default : 'forms',
			},
			routeName : {
				type : String,
				default : 'formsadd',
			},
			apiPath : {
				type : String,
				default : 'forms/add',
			},
		},
		data() {
            return {
				formData: {
					application_id: "", field_group_id: "", revision_no: "", revion_date: "", 
				},
				revion_datePicker:false
			}
		},
		computed: {
			pageTitle:{
				get: function () {
					return "Add New Forms"
				}
			}
		},
		meta () {
			return {
				title: this.pageTitle
			}
		},
		methods: {
			...mapActions('forms', ['saveRecord']),
			async startSaveRecord(){
				const isFormValid = await this.$refs.observer.validate();
				if(isFormValid){
					this.saving = true;
					let payload = this.normalizedFormData();
					let url = this.apiUrl;
					let data = { url, payload  }
					this.saveRecord(data).then((response) => {
						this.record = response.data;
						this.id = this.record['id'];
						this.saving = false;
						this.resetForm();
						this.closeDialogs();// close page dialog that if opened
						this.flashMsg(this.msgAfterSave);
						this.navigateTo(`/forms`);
					},
					 (response) => {
						this.saving = false;
						this.showPageRequestError(response);
					});
				}
			},
			resetForm (){
				this.formData = {application_id: "", field_group_id: "", revision_no: "", revion_date: "", };
				requestAnimationFrame(() => {
					this.$refs.observer.reset();
				});
				this.$EventBus.$emit("resetForm");
			},
		},
		mounted() {
		},
	};
</script>
<style scoped>
</style>
