<template>
    <div class="main-page">
        <template v-if="showHeader">
            <q-card   class="page-section q-py-sm q-px-md q-mb-md nice-shadow-18">
                <div class="container">
                    <div class="row justify-between q-col-gutter-md">
                        <div class="col-12 col-md-auto ">
                            <div class="">
                                <div class="row  items-center q-col-gutter-sm q-px-sm">
                                    <div class="col">
                                        <div class="text-h6 text-primary">Add New Amateur Operator Applications</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </q-card>
        </template>
        <section class="page-section q-mb-md">
            <div class="container">
                <div class="row q-col-gutter-md">
                    <div class="col-md-9 col-12 comp-grid">
                        <q-card   class="q-pa-md nice-shadow-18">
                            <div>
                                <ValidationObserver ref="observer" v-slot="{ invalid }" tag="form" @submit.prevent="startSaveRecord()" @reset="resetForm">
                                    <div class="row q-col-gutter-md">
                                        <div class="col-12">
                                            <div class="row">
                                                <div class="col-sm-3 col-12">
                                                    Submitted Application *
                                                </div>
                                                <div class="col-sm-9 col-12">
                                                    <api-data-source @loaded="response => mapOptionField(response, 'submitted_application_id')"  api-path="components_data/submitted_application_id_option_list"  v-slot="req">
                                                        <ValidationProvider :rules="{required:true}" name="Submitted Application" v-slot="{ errors, invalid, validated }">
                                                            <q-select   :loading="req.loading"  outlined dense  ref="ctrlsubmitted_application_id" emit-value map-options  v-model="formData.submitted_application_id" :options="req.response" label="Submitted Application"  :error="invalid && validated" :error-message="errors[0]" >
                                                            <template v-slot:option="scope">
                                                                <q-item v-bind="scope.itemProps" v-on="scope.itemEvents">
                                                                    <q-item-section v-if="scope.opt.image" avatar>
                                                                        <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                    </q-item-section>
                                                                    <q-item-section>
                                                                        <q-item-label />{{ scope.opt.label }}
                                                                            <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                        </q-item-section>
                                                                    </q-item>
                                                                </template>
                                                                <template v-slot:selected-item="scope">
                                                                    <q-item  v-if="scope.opt">
                                                                        <q-item-section v-if="scope.opt.image" avatar>
                                                                            <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                        </q-item-section>
                                                                        <q-item-section>
                                                                            <q-item-label />{{ scope.opt.label }}
                                                                                <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                            </q-item-section>
                                                                        </q-item>
                                                                    </template>
                                                                    </q-select> 
                                                                </ValidationProvider>
                                                            </api-data-source>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="col-12">
                                                    <div class="row">
                                                        <div class="col-sm-3 col-12">
                                                            Amateur Doc Type Id *
                                                        </div>
                                                        <div class="col-sm-9 col-12">
                                                            <api-data-source @loaded="response => mapOptionField(response, 'amateur_doc_type_id')"  api-path="components_data/amateur_doc_type_id_option_list"  v-slot="req">
                                                                <ValidationProvider :rules="{required:true}" name="Amateur Doc Type Id" v-slot="{ errors, invalid, validated }">
                                                                    <q-select   :loading="req.loading"  outlined dense  ref="ctrlamateur_doc_type_id" emit-value map-options  v-model="formData.amateur_doc_type_id" :options="req.response" label="Amateur Doc Type Id"  :error="invalid && validated" :error-message="errors[0]" >
                                                                    <template v-slot:option="scope">
                                                                        <q-item v-bind="scope.itemProps" v-on="scope.itemEvents">
                                                                            <q-item-section v-if="scope.opt.image" avatar>
                                                                                <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                            </q-item-section>
                                                                            <q-item-section>
                                                                                <q-item-label />{{ scope.opt.label }}
                                                                                    <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                                </q-item-section>
                                                                            </q-item>
                                                                        </template>
                                                                        <template v-slot:selected-item="scope">
                                                                            <q-item  v-if="scope.opt">
                                                                                <q-item-section v-if="scope.opt.image" avatar>
                                                                                    <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                                </q-item-section>
                                                                                <q-item-section>
                                                                                    <q-item-label />{{ scope.opt.label }}
                                                                                        <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                                    </q-item-section>
                                                                                </q-item>
                                                                            </template>
                                                                            </q-select> 
                                                                        </ValidationProvider>
                                                                    </api-data-source>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div class="col-12">
                                                            <div class="row">
                                                                <div class="col-sm-3 col-12">
                                                                    Amateur Station Type Id *
                                                                </div>
                                                                <div class="col-sm-9 col-12">
                                                                    <api-data-source @loaded="response => mapOptionField(response, 'amateur_station_type_id')"  api-path="components_data/amateur_station_type_id_option_list"  v-slot="req">
                                                                        <ValidationProvider :rules="{required:true}" name="Amateur Station Type Id" v-slot="{ errors, invalid, validated }">
                                                                            <q-select   :loading="req.loading"  outlined dense  ref="ctrlamateur_station_type_id" emit-value map-options  v-model="formData.amateur_station_type_id" :options="req.response" label="Amateur Station Type Id"  :error="invalid && validated" :error-message="errors[0]" >
                                                                            <template v-slot:option="scope">
                                                                                <q-item v-bind="scope.itemProps" v-on="scope.itemEvents">
                                                                                    <q-item-section v-if="scope.opt.image" avatar>
                                                                                        <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                                    </q-item-section>
                                                                                    <q-item-section>
                                                                                        <q-item-label />{{ scope.opt.label }}
                                                                                            <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                                        </q-item-section>
                                                                                    </q-item>
                                                                                </template>
                                                                                <template v-slot:selected-item="scope">
                                                                                    <q-item  v-if="scope.opt">
                                                                                        <q-item-section v-if="scope.opt.image" avatar>
                                                                                            <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                                        </q-item-section>
                                                                                        <q-item-section>
                                                                                            <q-item-label />{{ scope.opt.label }}
                                                                                                <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                                            </q-item-section>
                                                                                        </q-item>
                                                                                    </template>
                                                                                    </q-select> 
                                                                                </ValidationProvider>
                                                                            </api-data-source>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                                <div class="col-12">
                                                                    <div class="row">
                                                                        <div class="col-sm-3 col-12">
                                                                            Certicate Application Type Id *
                                                                        </div>
                                                                        <div class="col-sm-9 col-12">
                                                                            <api-data-source @loaded="response => mapOptionField(response, 'certicate_application_type_id')"  api-path="components_data/certicate_application_type_id_option_list"  v-slot="req">
                                                                                <ValidationProvider :rules="{required:true}" name="Certicate Application Type Id" v-slot="{ errors, invalid, validated }">
                                                                                    <q-select   :loading="req.loading"  outlined dense  ref="ctrlcerticate_application_type_id" emit-value map-options  v-model="formData.certicate_application_type_id" :options="req.response" label="Certicate Application Type Id"  :error="invalid && validated" :error-message="errors[0]" >
                                                                                    <template v-slot:option="scope">
                                                                                        <q-item v-bind="scope.itemProps" v-on="scope.itemEvents">
                                                                                            <q-item-section v-if="scope.opt.image" avatar>
                                                                                                <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                                            </q-item-section>
                                                                                            <q-item-section>
                                                                                                <q-item-label />{{ scope.opt.label }}
                                                                                                    <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                                                </q-item-section>
                                                                                            </q-item>
                                                                                        </template>
                                                                                        <template v-slot:selected-item="scope">
                                                                                            <q-item  v-if="scope.opt">
                                                                                                <q-item-section v-if="scope.opt.image" avatar>
                                                                                                    <image-viewer :src="scope.opt.image" image-size="small"></image-viewer>
                                                                                                </q-item-section>
                                                                                                <q-item-section>
                                                                                                    <q-item-label />{{ scope.opt.label }}
                                                                                                        <q-item-label v-if="scope.opt.caption" caption>{{ scope.opt.caption }}</q-item-label>
                                                                                                    </q-item-section>
                                                                                                </q-item>
                                                                                            </template>
                                                                                            </q-select> 
                                                                                        </ValidationProvider>
                                                                                    </api-data-source>
                                                                                </div>
                                                                            </div>
                                                                        </div>
                                                                        <div class="col-12">
                                                                            <div class="row">
                                                                                <div class="col-sm-3 col-12">
                                                                                    Station Location *
                                                                                </div>
                                                                                <div class="col-sm-9 col-12">
                                                                                    <ValidationProvider :rules="{required:true}" name="Station Location" v-slot="{ errors, invalid, validated }">
                                                                                        <q-input outlined dense  ref="ctrlstation_location" v-model="formData.station_location"  label="Station Location" type="text" placeholder="Enter Station Location"   list="station_location_list"  
                                                                                        class="" :error="invalid && validated" :error-message="errors[0]">
                                                                                        </q-input>
                                                                                        <datalist id="station_location_list">
                                                                                        <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                                                        </datalist>
                                                                                    </ValidationProvider>
                                                                                </div>
                                                                            </div>
                                                                        </div>
                                                                        <div class="col-12">
                                                                            <div class="row">
                                                                                <div class="col-sm-3 col-12">
                                                                                    Call Sign *
                                                                                </div>
                                                                                <div class="col-sm-9 col-12">
                                                                                    <ValidationProvider :rules="{required:true}" name="Call Sign" v-slot="{ errors, invalid, validated }">
                                                                                        <q-input outlined dense  ref="ctrlcall_sign" v-model="formData.call_sign"  label="Call Sign" type="text" placeholder="Enter Call Sign"   list="call_sign_list"  
                                                                                        class="" :error="invalid && validated" :error-message="errors[0]">
                                                                                        </q-input>
                                                                                        <datalist id="call_sign_list">
                                                                                        <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                                                        </datalist>
                                                                                    </ValidationProvider>
                                                                                </div>
                                                                            </div>
                                                                        </div>
                                                                        <div class="col-12">
                                                                            <div class="row">
                                                                                <div class="col-sm-3 col-12">
                                                                                    Issued On *
                                                                                </div>
                                                                                <div class="col-sm-9 col-12">
                                                                                    <ValidationProvider :rules="{required:true}" name="Issued On" v-slot="{ errors, invalid, validated }">
                                                                                        <q-input outlined dense  v-model="formData.issued_on" :error="invalid && validated" :error-message="errors[0]" >
                                                                                        <template v-slot:prepend>
                                                                                            <q-icon name="date_range" class="cursor-pointer">
                                                                                            <q-popup-proxy transition-show="scale" transition-hide="scale">
                                                                                            <q-date     mask="YYYY-MM-DD HH:mm" v-model="formData.issued_on" />
                                                                                            </q-popup-proxy>
                                                                                            </q-icon>
                                                                                        </template>
                                                                                        <template v-slot:append>
                                                                                            <q-icon name="access_time" class="cursor-pointer">
                                                                                            <q-popup-proxy transition-show="scale" transition-hide="scale">
                                                                                            <q-time v-model="formData.issued_on" mask="YYYY-MM-DD HH:mm" />
                                                                                            </q-popup-proxy>
                                                                                            </q-icon>
                                                                                        </template>
                                                                                        </q-input>
                                                                                    </ValidationProvider>
                                                                                </div>
                                                                            </div>
                                                                        </div>
                                                                        <div class="col-12">
                                                                            <div class="row">
                                                                                <div class="col-sm-3 col-12">
                                                                                    Valid On *
                                                                                </div>
                                                                                <div class="col-sm-9 col-12">
                                                                                    <ValidationProvider :rules="{required:true}" name="Valid On" v-slot="{ errors, invalid, validated }">
                                                                                        <q-input outlined dense  v-model="formData.valid_on" :error="invalid && validated" :error-message="errors[0]" >
                                                                                        <template v-slot:prepend>
                                                                                            <q-icon name="date_range" class="cursor-pointer">
                                                                                            <q-popup-proxy transition-show="scale" transition-hide="scale">
                                                                                            <q-date     mask="YYYY-MM-DD HH:mm" v-model="formData.valid_on" />
                                                                                            </q-popup-proxy>
                                                                                            </q-icon>
                                                                                        </template>
                                                                                        <template v-slot:append>
                                                                                            <q-icon name="access_time" class="cursor-pointer">
                                                                                            <q-popup-proxy transition-show="scale" transition-hide="scale">
                                                                                            <q-time v-model="formData.valid_on" mask="YYYY-MM-DD HH:mm" />
                                                                                            </q-popup-proxy>
                                                                                            </q-icon>
                                                                                        </template>
                                                                                        </q-input>
                                                                                    </ValidationProvider>
                                                                                </div>
                                                                            </div>
                                                                        </div>
                                                                        <div class="col-12">
                                                                            <div class="row">
                                                                                <div class="col-sm-3 col-12">
                                                                                    Valid Until *
                                                                                </div>
                                                                                <div class="col-sm-9 col-12">
                                                                                    <ValidationProvider :rules="{required:true}" name="Valid Until" v-slot="{ errors, invalid, validated }">
                                                                                        <q-input outlined dense  v-model="formData.valid_until" :error="invalid && validated" :error-message="errors[0]" >
                                                                                        <template v-slot:prepend>
                                                                                            <q-icon name="date_range" class="cursor-pointer">
                                                                                            <q-popup-proxy transition-show="scale" transition-hide="scale">
                                                                                            <q-date     mask="YYYY-MM-DD HH:mm" v-model="formData.valid_until" />
                                                                                            </q-popup-proxy>
                                                                                            </q-icon>
                                                                                        </template>
                                                                                        <template v-slot:append>
                                                                                            <q-icon name="access_time" class="cursor-pointer">
                                                                                            <q-popup-proxy transition-show="scale" transition-hide="scale">
                                                                                            <q-time v-model="formData.valid_until" mask="YYYY-MM-DD HH:mm" />
                                                                                            </q-popup-proxy>
                                                                                            </q-icon>
                                                                                        </template>
                                                                                        </q-input>
                                                                                    </ValidationProvider>
                                                                                </div>
                                                                            </div>
                                                                        </div>
                                                                    </div>
                                                                    <div v-if="showSubmitButton" class="text-center q-my-md">
                                                                        <q-btn type="submit"    :rounded="false"  color="primary"  no-caps  unelevated   :disabled="invalid" icon="send" :loading="saving">
                                                                            <q-spinner-oval slot="loading" /> Submit
                                                                        </q-btn>
                                                                    </div>
                                                                </ValidationObserver>
                                                                <slot :submit="submit" :saving="saving"></slot>
                                                            </div>
                                                        </q-card>
                                                    </div>
                                                </div>
                                            </div>
                                        </section>
                                    </div>
                                </template>
                                <script>
	import { PageMixin } from "../../mixins/page.js";
	import { AddPageMixin } from "../../mixins/addpage.js";
	import { mapActions } from "vuex";
	export default {
		name: 'addAmateuroperatorapplicationsPage',
		components: {
		},
		mixins: [PageMixin, AddPageMixin ],
		props:{
			pageName : {
				type : String,
				default : 'amateur_operator_applications',
			},
			routeName : {
				type : String,
				default : 'amateur_operator_applicationsadd',
			},
			apiPath : {
				type : String,
				default : 'amateur_operator_applications/add',
			},
		},
		data() {
            return {
				formData: {
					submitted_application_id: "", amateur_doc_type_id: "", amateur_station_type_id: "", certicate_application_type_id: "", station_location: "", call_sign: "", issued_on: "", valid_on: "", valid_until: "", 
				},
				issued_onPicker:false,valid_onPicker:false,valid_untilPicker:false
			}
		},
		computed: {
			pageTitle:{
				get: function () {
					return "Add New Amateur Operator Applications"
				}
			}
		},
		meta () {
			return {
				title: this.pageTitle
			}
		},
		methods: {
			...mapActions('amateur_operator_applications', ['saveRecord']),
			async startSaveRecord(){
				const isFormValid = await this.$refs.observer.validate();
				if(isFormValid){
					this.saving = true;
					let payload = this.normalizedFormData();
					let url = this.apiUrl;
					let data = { url, payload  }
					this.saveRecord(data).then((response) => {
						this.record = response.data;
						this.id = this.record['id'];
						this.saving = false;
						this.resetForm();
						this.closeDialogs();// close page dialog that if opened
						this.flashMsg(this.msgAfterSave);
						this.navigateTo(`/amateur_operator_applications`);
					},
					 (response) => {
						this.saving = false;
						this.showPageRequestError(response);
					});
				}
			},
			resetForm (){
				this.formData = {submitted_application_id: "", amateur_doc_type_id: "", amateur_station_type_id: "", certicate_application_type_id: "", station_location: "", call_sign: "", issued_on: "", valid_on: "", valid_until: "", };
				requestAnimationFrame(() => {
					this.$refs.observer.reset();
				});
				this.$EventBus.$emit("resetForm");
			},
		},
		mounted() {
		},
	};
</script>
<style scoped>
</style>
